.Button{
    background: #f0a9e0;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    width: 25%;
    font-size: 24px;
    color: rgba(0, 0, 0, 0.5);
    height: 20%;
    min-width: 25%;
}
.Button[data-size="2"]{
    height: 40%;
}
.Button[data-value="null"]{
    pointer-events: none;
}
.Button:hover{
    background: #f77cda;
    cursor: pointer;
}